// Imports
@import 'mixins';
@import 'colors';
@import 'type';
@import 'layout';
@import 'navigation';
@import 'tables';
@import 'forms';
@import 'promos';
@import 'footer';
@import 'accordian';
@import 'modal';
@import 'mediaQry';

// 3 Color Variables
$menu1: red;
$menu2: blue;
$menu3: green;


$base: $menu1;

@for $i from 1 through 37 {
  .lighter li:nth-child(#{$i}) {
    background: $base;
    list-style-type: none;

    a {
      color: black;
      text-decoration: none;
      font-style: normal;
    }
  }
  $base: lighten($base, 1.5%);
  $i: $i + 1;
}

$base: $menu2;
@for $i from 1 through 37 {
  .darker li:nth-child(#{$i}) {
    background: $base;
    list-style-type: none;

    a {
      color: white;
      text-decoration: none;
      font-style: normal;
    }
  }
  $base: darken($base, 1.5%);
  $i: $i + 1;
}

$base: $menu1;
$degree: 0;
@for $i from 1 through 37 {
  .rainbow li:nth-child(#{$i}) {
    background: $base;
    list-style-type: none;

    a {
      color: white;
      text-decoration: none;
      font-style: normal;
    }
  }
  $base: adjust-hue(red, $degree);
  $degree: $degree + 10;
  $i: $i + 1;
}

$base: $menu1;
$degree: 100;
@for $i from 1 through 37 {
  .muted li:nth-child(#{$i}) {
    background: $base;
    list-style-type: none;

    a {
      color: white;
      text-decoration: none;
      font-style: normal;
    }
  }
  $base: hsl(0, $degree, 50);
  $degree : $degree - 2.9;
  $i: $i + 1;
}
